.navbarContainer {
    height: 60px;
    width: 100%;
    background-color: #18f27a;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 999;
  }
  .navbarLeft {
    flex: 4;
  }
  
  .logo {
    margin-left: 3rem;
    font-size: 1.7rem;
    font-weight: bold;
    color: white;
    cursor: pointer;
  }
  
  .navbarCenter {
    flex: 4;
  }
  
  .searchbar {
    width: 100%;
    height: 30px;
    background-color: white;
    border-radius: 30px;
    display: flex;
    align-items: center;
  }
  
  .searchIcon {
    font-size: 20px !important;
    margin-left: 10px;
  }
  
  .searchInput {
    border: none;
    width: 70%;
  }
  
  .searchInput:focus {
    outline: none;
  }
  
  .navbarRight {
    flex: 4;
    display: flex;
    align-items: center;
    justify-content: space-around;
    color: white;
    margin-left: 20rem;
  }
  
  .navbarLink {
    margin-right: 10px;
    font-size: 14px;
    cursor: pointer;
  }
  
  .navbarIcons {
    display: flex;
  }
  
  .navbarIconItem {
    margin-right: 15px;
    cursor: pointer;
    position: relative;
  }
  
  .navbarIconBadge {
    width: 15px;
    height: 15px;
    background-color: red;
    border-radius: 50%;
    color: white;
    position: absolute;
    top: -5px;
    right: -5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
  }
  
  .navbarImg {
    width: 43px;
    height: 43px;
    border-radius: 50%;
    object-fit: cover;
    cursor: pointer;
    border: 2px solid white;
    margin-right: .5rem;
  }

